		},
		methods:{
			getGoodsList(params){
				this.$http.get('/home/data',{params}).then(res => console.log(res.data.data.sort))
			}
		},
		watch:{
			switch_code(){
				// console.log("数据改变了,当前是" + this.switch_code);
				if(this.switch_code == 0){
					this.params.type = 'pop'
					this.getGoodsList(this.params)
				}else if(this.switch_code == 1){
					this.params.type = 'sell'
					this.getGoodsList(this.params)
				}else if(this.switch_code == 2){
					this.params.type = 'new'
					this.getGoodsList(this.params)
				}
			}
